@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  box-sizing: border-box;
}

#focus {
  width: calc(600px + 10vw);
  height: 76vh;
  margin-top: -34vh;
}
#focus .top_row ul .unfocues_file {
  color: #547283;
  background-color: #181f2b;
}
#focus .top_row ul .focues_file {
  color: #12161d;
  background-color: #ee5a35;
}
#focus .sum {
  display: none;
  visibility: hidden;
}
#focus .desc {
  display: block;
  visibility: visible;
}

.heading {
  background-color: #181f2b;
  height: calc(54vh - 80px);
  width: 100%;
  transition: left 0.5s;
  transition: right 0.5s;
  position: relative;
  background-image: url("../imgs/main_background.png");
}
.heading .rust_log {
  position: absolute;
  width: 200px;
  height: 200px;
}
.heading .rust_log1 {
  left: -20px;
  bottom: -29px;
  transform: rotate(20deg);
  transform: scale(200px);
}
.heading .rust_log2 {
  right: -20px;
  top: 20px;
  transform: rotate(-16deg);
}
.heading .heading-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: left 0.5s;
  left: calc(50% - (55vh - 4vw) / 2);
  top: calc(50% - 11.5vh);
  width: calc(55vh - 4vw);
  height: 23vh;
}
.heading .heading-container h1 {
  color: #E0FBFC;
  font-size: calc(25vh - 4vw);
  margin-left: 11.5vh;
}
.heading .heading-container h1 .cursor {
  width: calc(55vh - 4vw);
  height: 23vh;
  z-index: 1;
  animation: blink 1.1s infinite 1s;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
.heading .heading-container h2 {
  color: #E0FBFC;
  font-size: 2vh;
}

body {
  min-width: 375px;
  background-color: #12161d;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
body .cards {
  width: 100%;
  height: 100%;
  position: relative;
}
body .cards .t_window_div {
  border: 2px solid #314957;
  background-color: #12161d;
  border-radius: 5px;
  font-size: 16px;
  transition: width 500ms, height 500ms, margin 500ms;
  overflow-y: none;
}
body .cards .t_window_div .top_row {
  width: 100%;
  height: 25px;
  background-color: #181f2b;
  float: right;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #2a3342;
}
body .cards .t_window_div .top_row ul {
  list-style: none;
}
body .cards .t_window_div .top_row ul .unfocues_file {
  color: #12161d;
  background-color: #ee5a35;
  border-radius: 2px;
}
body .cards .t_window_div .top_row ul .focues_file {
  color: #547283;
  border-radius: 2px;
  background-color: #181f2b;
}
body .cards .t_window_div .top_row ul li {
  font-size: 17px;
  float: left;
  margin-left: 10px;
  font-size: 12px;
  color: #547283;
}
body .cards .t_window_div .title_bar {
  border-radius: 3px;
  height: 30px;
  width: 100%;
  background-color: #2a3342;
  color: #E0FBFC;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #314957;
}
body .cards .t_window_div .title_bar p {
  font-size: 16px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 2px;
}
body .cards .t_window_div .title_bar .icons {
  height: 100%;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
body .cards .t_window_div .title_bar .icons .button {
  height: 90%;
  width: 30px;
  margin: 2px;
  background-size: 15px;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2px;
  transition: background-color 500ms;
}
body .cards .t_window_div .title_bar .icons .button:hover {
  background-color: #ee5a35;
  border-radius: 5px;
}
body .cards .t_window_div .title_bar .icons .minimalize {
  background-image: url(../imgs/minim.png);
}
body .cards .t_window_div .title_bar .icons .fullscreen {
  background-image: url(../imgs/fulls.png);
}
body .cards .t_window_div .title_bar .icons .close {
  background-image: url(../imgs/close.png);
}
body .cards .line_numbers {
  margin-top: 1px;
  height: calc(100% - 30px - 25px);
  width: 30px;
  float: left;
  border-right: 1px solid #2a3342;
  background-color: #181f2b;
  overflow-y: hidden;
}
body .cards .line_numbers ol {
  list-style-type: decimal;
  list-style-position: inside;
}
body .cards .line_numbers ol li {
  margin-right: 2px;
  text-align: right;
  display: block;
  color: #547283;
  counter-increment: customlistcounter;
}
body .cards .line_numbers ol li:before {
  content: counter(customlistcounter) " ";
  counter-increment: item;
}
body .cards h1 {
  color: #3D5A80;
  font-size: 16px;
}
body .cards h2 {
  font-size: 16px;
  color: #ee5a35;
}
body .cards p {
  font-size: 16px;
  color: #E0FBFC;
}
body .cards a {
  font-size: 16px;
  color: #314957;
  text-decoration: none;
  transition: all 300ms;
}
body .cards a:hover {
  color: #3D5A80;
}
body .navbar {
  position: relative;
  width: 100%;
  height: 60px;
  background-color: #2a3342;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
body .navbar ul {
  list-style: none;
}
body .navbar ul li {
  font-size: 17px;
  float: right;
  margin: 10px;
}
body .navbar ul li a {
  color: #12161d;
  text-decoration: none;
}
body main {
  width: 100%;
  height: 100%;
}
body main .index {
  position: absolute;
  background-color: #12161d;
  width: 100%;
}
body main .index .cards {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
body main .index .cards .t_window_div {
  width: 600px;
  margin: 10px;
  height: 46vh;
}
body main .index .cards .t_window_div .content {
  transition: visibility 200ms;
}
body main .index .cards .t_window_div .sum {
  display: block;
  visibility: visible;
}
body main .index .cards .t_window_div .desc {
  display: none;
  visibility: hidden;
}
body main .index .navbar {
  margin-top: 0;
  width: 100%;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2a3342;
  display: flex;
  justify-content: end;
  align-items: center;
}
body main .index .navbar ul li {
  font-size: 17px;
  float: right;
  margin-right: 30px;
}
body main .index .navbar ul li a {
  color: #F7F4ED;
  text-decoration: none;
}
body main .status {
  position: absolute;
  width: 100%;
  height: calc(100% - 60px);
}
body main .status .cards {
  display: grid;
  background-color: #181f2b;
  color: #444;
  padding: 10px;
  transition: 300ms;
}
body main .status .cards .t_window_div {
  border-radius: 5px;
}
body main .status .cards .docs {
  grid-column: 1;
  overflow: hidden;
  grid-row: 1;
}
body main .status .cards .docs .title_bar {
  width: 100%;
  border-radius: 5px 5px 0px 0px;
}
body main .status .cards .docs .t_window_inner {
  position: relative;
  height: calc(100% - 30px);
  width: 100%;
  display: flex;
}
body main .status .cards .docs .t_window_inner .t_window_inner_nav {
  margin: 10px;
  width: 20%;
  background-color: #2a3342;
  height: calc(100% - 20px);
  border-radius: 5px;
  transition: all 0.5s;
}
body main .status .cards .docs .t_window_inner .t_window_inner_nav .normal li {
  list-style: none;
  padding: 10px;
  color: #F7F4ED;
  transition: all 0.3s;
}
body main .status .cards .docs .t_window_inner .t_window_inner_nav .normal li:hover {
  cursor: pointer;
  background-color: #314957;
  transition: all 0.3s;
  border-radius: 5px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_nav #selected {
  background-color: #547283;
  border-radius: 5px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content {
  margin: 10px 10px 10px 0px;
  background-color: #181f2b;
  border-radius: 5px;
  box-sizing: content-box;
  width: 100%;
  height: calc(100% - 20px);
  display: flex;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  border-radius: 5px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_header {
  background-color: rebeccapurple;
  width: 100%;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_header h3 {
  color: #E0FBFC;
  font-size: 5.5vh;
  text-align: center;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content {
  background-color: palevioletred;
  width: 100%;
  min-height: fit-content;
  display: flex;
  flex-direction: column;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .card_description {
  width: 80%;
  margin: 0px auto;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content h3 {
  margin-top: 30px;
  color: #E0FBFC;
  font-size: 3.5vh;
  text-align: center;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards {
  background-color: #00798C;
  margin-top: 30px;
  padding: 30px;
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards .page_card {
  border-radius: 5px;
  margin: 20px;
  width: 300px;
  height: 200px;
  background-color: #181f2b;
  position: relative;
  transition: all 0.3s;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards .page_card .page_card_img {
  background-color: red;
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 150px;
  border-radius: 5px 5px 0px 0px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards .page_card .page_card_description {
  background-color: blue;
  bottom: 50px;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards .page_card .page_card_title {
  background-color: green;
  bottom: 0px;
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 50px;
  border-radius: 0px 0px 5px 5px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_content .page_cards .page_card:hover {
  cursor: pointer;
  height: 300px;
}
body main .status .cards .docs .t_window_inner .t_window_inner_content .window_content .page_footer {
  background-color: pink;
  width: 100%;
  height: 20%;
}
body main .status .cards .docs .t_window_inner .content {
  display: none;
}
body main .learn {
  position: absolute;
  width: 100%;
  height: calc(100% - 60px);
}
body main .learn .cards {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 100% calc(40% - 10px);
  grid-template-rows: 70% calc(30% - 10px);
  background-color: #181f2b;
  color: #444;
  padding: 10px;
  transition: 300ms;
}
body main .learn .cards .t_window_div .content .sub_content {
  display: none;
}
body main .learn .cards .docs {
  grid-column: 1;
  grid-row: 1/3;
  overflow-y: scroll;
}
body main .learn .cards .docs .title_bar {
  position: absolute;
  width: calc(100% - 24px);
  transition: all 300ms;
}
body main .learn .cards .docs .learn-pages {
  margin-top: 30px;
  float: left;
  width: 272px;
  height: calc(100% - 30px - 17px - 4px);
  background-color: #181f2b;
  position: absolute;
  transition: width 300ms;
}
body main .learn .cards .docs .learn-pages .menu-button {
  display: none;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 50px;
  height: 40px;
  margin-top: 5px;
}
body main .learn .cards .docs .learn-pages .menu-button .line {
  width: 35px;
  height: 5px;
  border-radius: 3px;
  background-color: #314957;
}
body main .learn .cards .docs .learn-pages ul {
  list-style: none;
}
body main .learn .cards .docs .learn-pages ul li {
  font-size: 18px;
  height: 40px;
  width: 65%;
  color: #314957;
  padding: 7px;
  background-color: rgba(0, 0, 0, 0);
  transition: width 500ms, background-color 500ms;
}
body main .learn .cards .docs .learn-pages ul #selected {
  background-color: #2a3342;
  width: 100%;
  text-align: right;
}
body main .learn .cards .docs .content {
  width: calc(100% - 272px);
  height: calc(100% - 30px);
  margin-top: 30px;
  float: right;
  transition: width 300ms;
}
body main .learn .cards .docs .content code {
  background-color: #181f2b;
  color: #314957;
  font-size: 17px;
  padding: 0px 5px;
}
body main .learn .cards .docs .content .rust-c {
  background-color: #2a3342;
  color: #ee5a35;
}
body main .learn .cards .term {
  grid-column: 2;
  grid-row: 2;
}
body main .learn .cards .editor {
  grid-column: 2;
  grid-row: 1;
}
body main .news {
  position: absolute;
  background-color: #ee5a35;
  width: 100%;
  height: calc(100% - 60px);
}
body main .news #news_div {
  width: 100%;
  height: calc(100% - 17px);
}

@media only screen and (max-width: 1200px) {
  .status .t_window_inner_nav {
    max-width: 50px;
    min-width: 50px;
  }
  .status .t_window_inner_nav .sandwitch {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    width: 50px;
    height: 40px;
  }
  .status .t_window_inner_nav .sandwitch .line {
    margin: 5px;
    width: 35px;
    height: 5px;
    border-radius: 3px;
    background-color: #181f2b;
  }
  .status .t_window_inner_nav .sandwitch:hover {
    cursor: pointer;
    transition: all 0.3s;
    background-color: #547283;
    border-radius: 5px;
  }
  .status .t_window_inner_nav .normal {
    display: none;
  }
  .status #menu_button_pressed {
    max-width: 200px !important;
    min-width: 200px !important;
  }
  .status #menu_button_pressed .normal {
    display: block;
  }
  .status #menu_button_pressed .sandwitch {
    display: none !important;
  }
  .cards {
    position: relative;
    flex-wrap: wrap;
  }
  #focus {
    width: 600px;
    height: 76vh;
    margin-top: 0;
  }
  .learn .docs .learn-pages {
    width: 50px !important;
  }
  .learn .docs .learn-pages ul {
    display: none;
  }
  .learn .docs .learn-pages .menu-button {
    display: flex !important;
  }
  .learn .docs .content {
    width: calc(100% - 50px) !important;
  }
  .learn #menu_button_pressed {
    width: 272px !important;
  }
  .learn #menu_button_pressed ul {
    display: block;
  }
  .learn #menu_button_pressed .menu-button {
    display: none !important;
  }
}
@media only screen and (max-width: 600px) {
  .editor {
    display: none;
  }
  .term {
    display: none;
  }
  .learn .cards {
    grid-template-columns: 100% 0% !important;
  }
  .learn .cards .docs {
    grid-column: 1/2 !important;
    grid-row: 1/3 !important;
  }
  .learn .cards .docs .title_bar {
    width: calc(100% - 24px) !important;
  }
}